<template>
    <div>
        <!-- justify contentsを中央揃えに -->
        <v-row justify="center">
            <v-col cols="12" sm="12">
                <v-sheet min-height="70vh" rounded="lg" class="sheet" id="scrolling-techniques-3">
                    <!-- ここに書く -->
                    <div v-for="(row, rowIndex) in this.filteredData" :key="rowIndex">
                        <div id="idou">

                        </div>
                        <p id="todayIdou" v-if="row.length >= 3">{{ constMonth }}月{{ row[0] }}日の移動販売車</p><br>
                        <p class="place" v-for="(placeLine, index) in place" :key="index">
                            {{ placeLine }}
                        </p><br>
                        <p class="place" v-for="(placeLine, index) in place2" :key="index">
                            {{ placeLine }}
                        </p><br>
                        <p>{{ row[2] }} {{ row[3] }}{{ row[4] }}</p>
                    </div>
                    </v-sheet>
                </v-col>
        </v-row>
    </div>
</template>

<script>
export default {
    name: 'KitchenCar',
    data: () => ({
      csvData: [], // CSVデータを保持する配列
      constDay: 0,
      constMonth: 0,
      place: [],
      place2: []
    }),
    mounted() {
        // 今日の日付を取得
        const today = new Date();
        const year = today.getFullYear;
        const day = today.getDate();
        const month = today.getMonth() + 1;

        console.log(year);

        this.constMonth = month;
        this.constDay = day;

        //場所の情報を配列に入れて表示させる
        this.place = ['八草　','　　　　　　　　　　　　　　　　　　　　　　　　　','自由が丘'];
        this.place2 = ['1号館前　','　　　　　　　　セントラルガーデン　', '　　　　　本館２階駐輪場前　'];
        
        // ページがマウントされた時にCSVを読み込む
        this.loadCSV();
        // console.log('today: ',this.constDay);    
    },
    computed: {
        filteredData() {
            // 特定の日付と一致する行だけを抽出する
            return this.csvData.filter((row) => parseInt(row[0]) === this.constDay);
        },
    },
    methods: {
        async loadCSV() {
            try {
                // CSVファイルの取得
                const response = await fetch('202211.csv'); // Assuming the CSV file is in the public directory
                const csvText = await response.text();
                
                // CSVデータを行ごとに分割
                const rows = csvText.split('\n');

                // CSVデータを二次元配列に変換
                this.csvData = rows.map((row) => row.split(','));

            } catch (error) {
                console.error('Error loading CSV:', error);
            }
        },
    },
} 
</script>

