<template>
    <div>
        <!-- justify contentsを中央揃えに -->
        <v-row justify="center">
            <v-col cols="12" sm="12">
                <v-sheet min-height="70vh" rounded="lg" class="sheet" id="scrolling-techniques-3">
                    <!-- ここに書く -->
                    <!-- <div>検索：<input type="text" v-model="search" /></div> -->
                    <div>
                        <h2>愛工大マップ</h2>
                            <select v-model="selected">
                            <option disabled value="">Please select one</option>
                            <option>現在地</option>
                            <option>愛工大</option>
                            <option>1号館</option>
                            <option>1号館別館</option>
                            <option>新2号館</option>
                            <option>3号館(C1)</option>
                            <option>3号館別館(C2)</option>
                            <option>4号館</option>
                            <option>4号館別館</option>
                            <option>5号館</option>
                            <option>6号館</option>
                            <option>7号館</option>
                            <option>8号館</option>
                            <option>9号館(G3)</option>
                            <option>10号館(G2)</option>
                            <option>11号館</option>
                            <option>12号館</option>
                            <option>13号館</option>
                            </select>
                            <!-- <span>Selected: {{ selected }}</span> -->
                    </div>
                    <!-- <div>
                        <button @click="getLocation">
                            現在地付近
                        </button>
                    </div>
                    <div>
                        <form>
                            <div>
                            <label>Latitude: </label>
                            <input type="text" v-model="latitude" />
                            </div>
                            <div>
                            <label>Longitude: </label>
                            <input type="text" v-model="longitude" />
                            </div>
                        </form>
                        <button class="btn" style="width: 200px; background: skyblue;" v-on:click="showMap">SHOW</button>
                    </div> -->
                    <div ref="map" id="mapArea" style="height: 400px;"></div>
                    
                </v-sheet>
            </v-col>
        </v-row>
    </div>
</template>




<script>
export default {
    name: 'SchoolMaps',
    data: () => ({
        latitude: 35.18360947059331,
        longitude: 137.11300434536787,
        selected: '愛工大',
    }),

    mounted() {
        this.showMap();
    },

    watch: {
        longitude() {
            this.showMap()
        },

        selected(newSelect) {
            if (newSelect ==  '現在地') {
                this.getLocation()
            }  else if (newSelect == '愛工大') {
                this.latitude = 35.18360947059331, 
                this.longitude = 137.11300434536787
            } else if (newSelect == '1号館') {
                this.latitude = 35.18393945476342, 
                this.longitude = 137.11122595608316
            } else if (newSelect == '1号館別館') {
                this.latitude = 35.184755757058426,
                this.longitude = 137.1109978157656
            } else if (newSelect == '新2号館') {
                this.latitude = 35.183703880504815, 
                this.longitude = 137.11419974761313
            } else if (newSelect == '3号館(C1)') {
                this.latitude = 35.183922211024395,
                this.longitude = 137.11504557393604
            } else if (newSelect == '3号館別館(C2)') {
                this.latitude = 35.18370320371449,
                this.longitude = 137.11509877225797
            } else if (newSelect == '4号館') {
                this.latitude = 35.18482994471485, 
                this.longitude = 137.1150659575287
            }  else if (newSelect == '4号館別館') {
                this.latitude = 35.18519724115331,
                this.longitude = 137.11532291082867
            }else if (newSelect == '5号館') {
                this.latitude = 35.18458493374964,
                this.longitude = 137.1140735165559
            } else if (newSelect == '6号館') {
                this.latitude = 35.18473046120468,
                this.longitude = 137.11610454506928 
            } else if (newSelect == '7号館') {
                this.latitude = 35.1846817556055,
                this.longitude = 137.11249795736597
            } else if (newSelect == '8号館') {
                this.latitude = 35.18420802854306,
                this.longitude = 137.11458722046635
            } else if (newSelect == '9号館(G3)') {
                this.latitude = 35.18475873791302, 
                this.longitude = 137.11156065100775
            } else if (newSelect == '10号館(G2)') {
                this.latitude = 35.18419280914104, 
                this.longitude = 137.110973746277
            } else if (newSelect == '11号館') {
                this.latitude = 35.185232783157495, 
                this.longitude = 137.11593943875485
            } else if (newSelect == '12号館') {
                this.latitude = 35.18372755469411,
                this.longitude = 137.1134808811515
            } else if (newSelect == '13号館') {
                this.latitude = 35.184737832991345, 
                this.longitude = 137.1157716465662
            }
        }
    },

    methods: {

    showMap() {
        // APIキーを使用しスクリプトの呼び出すやつ
        const script = document.createElement('script');
        script.src = 'https://maps.googleapis.com/maps/api/js?key=AIzaSyD31Xh6RcYFzbDYNl05jZ9gp7ZZ9BZWni0&callback=initMap' 
        + "&libraries=places";
        script.async = true;
        document.head.appendChild(script);

        // スクリプト呼び出し後のコールバック
        window.initMap = () => {
            const myLatLng = { lat: Number(this.latitude), lng: Number(this.longitude) };
            const map = new window.google.maps.Map(this.$refs.map, {
            center: myLatLng,
            zoom: 18,
            });
            new window.google.maps.Marker({ 
            position: myLatLng, 
            map 
            });
        };
        },

        getLocation () {
            if (!navigator.geolocation) {
                
                alert('現在地情報を取得できませんでした。お使いのブラウザでは現在地情報を利用できない可能性があります。エリアを入力してください。')
                return
            }
            const options = {
                enableHighAccuracy: false,
                timeout: 5000,
                maximumAge: 0
            }
            navigator.geolocation.getCurrentPosition(this.success, this.error, options)
        },

        success (position) {
            // console.log("lng " + position.coords.latitude);
            // console.log("lat " + position.coords.longitude);
            // console.log("ロケーションを取得しました")
            
            this.latitude = position.coords.latitude;
            this.longitude = position.coords.longitude;
        },

        error (error) {
            switch (error.code) {
                case 1: //PERMISSION_DENIED
                    alert('位置情報の利用が許可されていません')
                    break
                case 2: //POSITION_UNAVAILABLE
                    alert('現在位置が取得できませんでした')
                    break
                case 3: //TIMEOUT
                    alert('タイムアウトになりました')
                    break
                default:
                    alert('現在位置が取得できませんでした')
                    break
            }
        },
    }
} 
</script>
